body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.middleAll {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}

.absoluteMiddle {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    position: absolute;
    margin:auto;
    top:0;
    bottom: 0;
    left:0;
    right:0;
}

.theHeading {
  margin-top: 2%;
  margin-bottom: 10%;
  color:grey;
}

span {
    padding: 1%;
}

.portalHeading {
  color: grey;
  cursor: pointer;
}

.headingAccount {
  text-align: center;
}

.errors {
  display: -webkit-flex;
  display: flex;
  color: red;
}

.settingHeading {
  margin-top: 1%;
  color:grey;
  text-align: center;
}

.form{
  width: 50%;
  margin: auto;
}

.form :hover { cursor: pointer; }

#bottom-widgets{
  position:fixed;
  bottom: 0px;  
  width: 100%;
}
